import SubSection from './SubSection';
import DishCard from './DishCard';

export default function Section({ title, data }) {
  if (title === 'RECOMENDACIONES') {
    return (
      <div className="recommendations">
        <h2>🌟 Nuestra selección para ti.</h2>
        {data.map(d => <DishCard key={d.id} dish={d} />)}
      </div>
    );
  }

  return (
    <div className="section">
      <h2>Las mejores {title.toLowerCase()}</h2>
      {Object.entries(data).map(([sub, dishes]) => (
        <SubSection key={sub} title={sub} dishes={dishes} />
      ))}
    </div>
  );
}
