// preloadModels.js
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

const modelsToPreload = [
  '/models/BruleeJapanese.glb',
  '/models/BurguerYBoneles.glb',
  '/models/BurritosTailandeses.glb',
  '/models/carne-jicama-arroz-salsa.glb',
  '/models/ChickenBurger.glb',
  '/models/Cocktail.glb',
  '/models/CrunchyBossBurguer.glb',
  '/models/DailyRoll.glb',
  '/models/Empanadas.glb',
  '/models/Fajitas.glb',
  '/models/FruitCocktail.glb',
  '/models/hotdog.glb',
  '/models/ItalianPizza.glb',
  '/models/KoreanBBQWings.glb',
  '/models/lemonade.glb',
  '/models/MeatballPizza.glb',
  '/models/MonsterCheese.glb',
  '/models/NACHOS.glb',
  '/models/PapasFrancesasOrden.glb',
  '/models/reebeye.glb',
  '/models/Sushi.glb',
  '/models/t-bone.glb'
];


const loader = new GLTFLoader();
const preloadCache = {};

export function preloadAllModels() {
  modelsToPreload.forEach((path) => {
    loader.load(process.env.PUBLIC_URL + path, (gltf) => {
      preloadCache[path] = gltf; // almacenamos pero no usamos aún
    });
  });
}
