import ARViewerButton from '../ARViewerButton';

export default function DishCard({ dish }) {
  return (
    <div className="dish-card">
      <img src={`${process.env.PUBLIC_URL}${dish.image}`} alt={dish.title} />
      <div className="card-content">
        <h4>{dish.title}</h4>
        <p>{dish.desc}</p>
        <div className="card-footer">
          <span>${dish.price}</span>
          <ARViewerButton 
            modelPath={dish.modelPath}
            rotationY={dish.rotationY}
            scale={dish.scale}
          />
        </div>
      </div>
    </div>
  );
}
