export default function NavBar({ sections, active, setActive }) {
  return (
    <nav className="nav-bar">
      {sections.map((sec) => (
        <button key={sec} className={sec === active ? 'active' : ''} onClick={() => setActive(sec)}>
          {sec}ㅤ|
        </button>
      ))}
    </nav>
  );
}
