import ARViewerButton from '../ARViewerButton';

export default function DishItem({ dish }) {
  return (
    <div className="dish-item">
      <div className="dish-info">
        <h4>{dish.title}</h4>
        <p>{dish.desc}</p>
      </div>
      <div className="dish-actions">
        <span className="price">${dish.price}</span>
        <ARViewerButton
          modelPath={dish.modelPath}
          rotationY={dish.rotationY}
          scale={dish.scale}
        />
      </div>
    </div>
  );
}
