import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { ARButton } from 'three/examples/jsm/webxr/ARButton';

let hiddenARButton = null;

export function startARViewer(modelRelativePath, rotationY = 0, scale = 1) {
  if (!navigator.xr) {
    alert('Lo sentimos, tu dispositivo no soporta AR. Por favor intenta con otro dispositivo.');
    return;
  }
  const modelURL = process.env.PUBLIC_URL + modelRelativePath;
  const animationURL = process.env.PUBLIC_URL + '/animations/MesaAR.json';

  let model3D = null;
  let modelPlaced = false;
  let modelInstance = null;
  let retryTimeout = null;
  let hitTestSource = null;
  let hitTestSourceRequested = false;
  let lottieInstance = null;

  const scene = new THREE.Scene();
  const camera = new THREE.PerspectiveCamera();
  scene.add(camera);

  const sizes = {
    width: window.innerWidth,
    height: window.innerHeight,
  };

  const light = new THREE.AmbientLight(0xffffff, 1.0);
  scene.add(light);

  const reticle = new THREE.Mesh(
    new THREE.RingGeometry(0.1, 0.13, 32).rotateX(-Math.PI / 2),
    new THREE.MeshStandardMaterial({ color: 0xffffff, opacity: 0.8, transparent: true })
  );
  reticle.visible = false;
  reticle.matrixAutoUpdate = false;
  scene.add(reticle);

  const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(window.devicePixelRatio);
  renderer.xr.enabled = true;

  const container = document.createElement('div');
  container.id = 'ar-container';
  Object.assign(container.style, {
    position: 'fixed', top: '0', left: '0', width: '100vw', height: '100vh', zIndex: '999'
  });
  document.body.appendChild(container);
  container.appendChild(renderer.domElement);

  hiddenARButton = ARButton.createButton(renderer, {
    requiredFeatures: ['hit-test', 'dom-overlay'],
    domOverlay: { root: container },
  });
  Object.assign(hiddenARButton.style, {
    display: 'none', visibility: 'hidden', position: 'absolute', pointerEvents: 'none'
  });
  container.appendChild(hiddenARButton);
  setTimeout(() => hiddenARButton.click(), 100);

  const ui = document.createElement('div');
  Object.assign(ui.style, {
    position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: '1001', pointerEvents: 'none', display: 'none'
  });
  container.appendChild(ui);

  const feedbackBox = document.createElement('div');
  feedbackBox.style.position = 'absolute';
  feedbackBox.style.top = '85%';
  feedbackBox.style.left = '50%';
  feedbackBox.style.transform = 'translate(-50%, -50%)';
  feedbackBox.style.background = 'rgba(0,0,0,0.7)';
  feedbackBox.style.color = '#fff';
  feedbackBox.style.padding = '14px 14px';
  feedbackBox.style.borderRadius = '10px';
  feedbackBox.style.fontSize = '16px';
  feedbackBox.style.pointerEvents = 'none';
  feedbackBox.style.textAlign = 'center';
  feedbackBox.style.justifyItems = 'center';
  feedbackBox.style.width = '80%';
  feedbackBox.style.maxWidth = '400px';
  

  const animContainer = document.createElement('div');
  animContainer.id = 'lottie-animation';
  animContainer.style.width = '180px';
  animContainer.style.height = '180px';
  animContainer.style.justifySelf = 'center';
  //animContainer.style.margin = '0 auto 12px';
  animContainer.style.pointerEvents = 'none';

  const msg = document.createElement('div');
  msg.id = 'tracking-msg';
  msg.innerText = '🔎 Reconociendo la mesa, por favor enfoca una superficie plana...';

  feedbackBox.appendChild(animContainer);
  feedbackBox.appendChild(msg);
  ui.appendChild(feedbackBox);

  import('lottie-web').then(Lottie => {
    lottieInstance = Lottie.default.loadAnimation({
      container: animContainer,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      path: animationURL
    });
  });

  const btnExit = document.createElement('button');
  btnExit.textContent = 'Salir';
  Object.assign(btnExit.style, {
    position: 'absolute', top: '5%', right: '15px', padding: '10px 16px',
    fontSize: '16px', borderRadius: '8px', border: 'none', cursor: 'pointer',
    background: 'rgba(0,0,0,0.6)', color: '#fff', pointerEvents: 'auto'
  });
  btnExit.onclick = () => exitAR();
  ui.appendChild(btnExit);

  const loader = new GLTFLoader();
  loader.load(modelURL, (gltf) => { model3D = gltf.scene; }, undefined,
    () => retryTimeout = setTimeout(() => loader.load(modelURL, () => {}), 1000)
  );

  const controller = renderer.xr.getController(0);
  controller.addEventListener('select', () => {
    if (!model3D || typeof model3D.clone !== 'function' || modelPlaced || !reticle.visible) return;
    modelInstance = model3D.clone();
    modelInstance.scale.set(0.8, 0.8, 0.8);
    modelInstance.rotation.y = rotationY;
    modelInstance.scale.multiplyScalar(scale);
    modelInstance.position.setFromMatrixPosition(reticle.matrix);
    modelInstance.name = 'model';
    scene.add(modelInstance);
    modelPlaced = true;
    reticle.visible = false;
    msg.innerText = '👆 Arrastra para mover, dos dedos para ajustar tamaño y rotar';
    animContainer.style.display = 'none';
  });
  scene.add(controller);

  function exitAR() {
    const session = renderer.xr.getSession();
    if (session) session.end();
    cleanup();
    window.location.reload();
  }

  function cleanup() {
    if (retryTimeout) clearTimeout(retryTimeout);
    model3D = null;
    modelInstance = null;
    modelPlaced = false;
    hitTestSource = null;
    hitTestSourceRequested = false;
    reticle.visible = false;
    msg.style.display = 'none';
    animContainer.style.display = 'none';
    ui.style.display = 'none';
  }

  renderer.xr.addEventListener('sessionstart', () => {
    ui.style.display = 'block';
  });

  renderer.xr.addEventListener('sessionend', () => {
    ui.style.display = 'none';
  });

  renderer.setAnimationLoop((_, frame) => {
    if (frame) {
      const referenceSpace = renderer.xr.getReferenceSpace();
      const session = renderer.xr.getSession();

      if (!hitTestSourceRequested) {
        session.requestReferenceSpace('viewer').then((refSpace) => {
          session.requestHitTestSource({ space: refSpace }).then((source) => {
            hitTestSource = source;
          });
        });
        session.addEventListener('end', () => {
          hitTestSourceRequested = false;
          hitTestSource = null;
        });
        hitTestSourceRequested = true;
      }

      if (hitTestSource && !modelPlaced) {
        const hitTestResults = frame.getHitTestResults(hitTestSource);
        const wasVisible = reticle.visible;

        if (hitTestResults.length > 0) {
          const hit = hitTestResults[0];
          reticle.visible = true;
          reticle.matrix.fromArray(hit.getPose(referenceSpace).transform.matrix);
        } else {
          reticle.visible = false;
        }

        if (reticle.visible && !wasVisible) {
          msg.innerText = '👆 Toque para colocar el plato';
          animContainer.style.display = 'none';
        }

        if (!reticle.visible && wasVisible) {
          msg.innerText = '🔎 Reconociendo la mesa, por favor enfoca una superficie plana...';
          animContainer.style.display = 'block';
        }
      }
    }
    renderer.render(scene, camera);
  });

  window.addEventListener('resize', () => {
    sizes.width = window.innerWidth;
    sizes.height = window.innerHeight;
    camera.aspect = sizes.width / sizes.height;
    camera.updateProjectionMatrix();
    renderer.setSize(sizes.width, sizes.height);
    renderer.setPixelRatio(window.devicePixelRatio);
  });

  let lastTouch = null;
  let lastTouches = null;

  renderer.domElement.addEventListener('touchstart', (e) => {
    if (!modelPlaced) return;
    if (e.touches.length === 1) {
      lastTouch = e.touches[0];
      lastTouches = null;
    } else if (e.touches.length === 2) {
      lastTouch = null;
      lastTouches = sortTouchesByY(e.touches);
    }
  }, { passive: true });

  renderer.domElement.addEventListener('touchmove', (e) => {
    if (!modelPlaced || !modelInstance) return;
    if (e.touches.length === 1 && lastTouch) {
      const touch = e.touches[0];
      const dx = (touch.clientX - lastTouch.clientX) / window.innerWidth;
      const dy = (touch.clientY - lastTouch.clientY) / window.innerHeight;
      const scale = 0.8;
      modelInstance.position.x += dx * scale;
      modelInstance.position.z += dy * scale;
      lastTouch = touch;
    } else if (e.touches.length === 2) {
      const [tTop, tBottom] = sortTouchesByY(e.touches);
      const [ltTop, ltBottom] = lastTouches;
      const dxTop = tTop.clientX - ltTop.clientX;
      const dxBottom = tBottom.clientX - ltBottom.clientX;
      const dyTop = tTop.clientY - ltTop.clientY;
      const dyBottom = tBottom.clientY - ltBottom.clientY;
      if (dyTop * dyBottom < 0) {
        const prevDist = Math.abs(ltTop.clientY - ltBottom.clientY);
        const newDist = Math.abs(tTop.clientY - tBottom.clientY);
        const delta = newDist - prevDist;
        const scaleFactor = 1 + delta / 300;
        const newScale = modelInstance.scale.clone().multiplyScalar(scaleFactor);
        newScale.clampScalar(0.3, 2.0);
        modelInstance.scale.copy(newScale);
      }
      if (dxTop * dxBottom < 0) {
        const angleDelta = (dxTop - dxBottom) * 0.005;
        modelInstance.rotation.y += -angleDelta;
      }
      lastTouches = [tTop, tBottom];
    }
  }, { passive: true });

  renderer.domElement.addEventListener('touchend', () => {
    lastTouch = null;
    lastTouches = null;
  });

  function sortTouchesByY(touches) {
    return [...touches].sort((a, b) => a.clientY - b.clientY);
  }
}