import React from 'react';
import { startARViewer } from '../ar/ARViewer';

export default function ARViewerButton({ modelPath, rotationY = 0, scale = 1 }) {
  const handleClick = () => startARViewer(modelPath, rotationY, scale);

  return (
    <button className="ar-btn" onClick={handleClick}>
      VER AR
    </button>
  );
}
